.container {
  margin-top: 60px;
  padding-bottom: 60px;
  background-color: #0f0d10;
  font-weight: 300;
}
.container h3 {
  color: white;
  font-weight: 300;
  font-size: 40px;
  text-align: center;
  padding: 90px 0 60px;
}
.inputsContainer {
  max-width: 1000px;
  padding: 0 50px;
  margin: 0 auto;
}
.labelContainer {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-bottom: 90px;
}
.labelContainer::after {
  content: "";
  width: 100%;
  height: 2px;
  background: white;
  position: absolute;
  transform: translateY(50%);
  transition: background 0.1s ease-in-out;
  bottom: 50%;
  z-index: 1;
}
.labelInput {
  z-index: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10px;
  height: 10px;
  background-color: white;
  transition: background-color 0.1s ease;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
  box-shadow: 0 0 3px black;
}
.labelInput:active {
  background-color: #999;
}
.labelSpan {
  display: inline-block;
  position: absolute;
  width: 24px;
  height: 24px;
  border: 2px solid white;
  border-radius: 50%;
  transition: opacity 0.2s ease, transform 0.5s ease, background-color 0.3s ease;
  transform: scale(0%, 0%);
}
.inputRadio {
  display: none;
}
.inputRadio:checked + .labelSpan {
  transform: scale(100%, 100%);
}
.inputTitulo {
  min-width: 20px;
  text-align: center;
  position: absolute;
  color: white;
  top: 24px;
  outline: none;
  user-select: none;
}
.errorInput::after {
  background-color: red;
}
.errorInput label {
  background-color: red;
}
.labelContainerTitulo {
  color: white;
  margin-bottom: 20px;
}
.FormContainer {
  padding: 60px 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
.FormInputs {
  display: flex;
  flex-direction: column;
  color: white;
  line-height: 35px;
  font-size: 18px;
  cursor: pointer;
  align-items: center;
}
.FormInput {
  margin-bottom: 60px;
  width: 250px;
  height: 60px;
  text-align: center;
  background-color: transparent;
  font-size: 16px;
  border: 1px solid transparent;
  border-bottom: 1px solid white;
  outline: none;
  color: white;
  padding: 20px 0;
  transition: 0.2s ease;
}
.FormInput:focus::placeholder {
  color: black;
}
.FormInput:focus {
  background-color: white;
  border: 1px solid black;
  color: black;
  box-shadow: 0 0 40px rgba(255, 255, 255, 0.171);
}
.FormInputs label {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.btnLink {
  user-select: none;
  display: flex;
  margin: 60px auto 0;
  text-decoration: none;
  justify-content: center;
  align-items: center;
}
.btnLink,
.FormBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  cursor: pointer;
  background-color: transparent;
  font-family: "Montserrat";
  font-weight: 500;
  font-size: 16px;
  width: 200px;
  height: 45px;
  border: 2px solid white;
  color: white;
  transition: all 0.3s ease;
}
.FormBtn p {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transition: 0.4s opacity ease;
}
.btnActive,
.btnLink:hover {
  background-color: white;
  color: black;
  box-shadow: 0 0 100px rgba(255, 255, 255, 0.075);
}
.ReCaptcha {
  margin-bottom: 60px;
  transition: box-shadow 0.3s ease;
}
.errorEmail {
  border-bottom: 1px solid red;
  color: red;
}
.errorEmail:focus {
  border: 2px solid red;
  color: red;
}
.errorEmail:focus::placeholder {
  color: red;
}
.errorEmail::placeholder {
  color: red;
}
.errorReCaptcha {
  box-shadow: 0 0 40px red;
}
.questionText {
  color: white;
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0.2px;
  text-align: justify;
  font-weight: 200;
  max-width: 80%;
  margin: auto;
  margin-bottom: 60px;
}
.Loader {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: hsla(0, 0%, 100%, 0.116);
  transition: opacity 0.3s ease;
  display: block;
  opacity: 0;
}
.Loader span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.Loader span::before {
  content: "";
  position: absolute;
  background-color: white;
  top: 0;
  left: 0;
  width: 100%;
  height: 15%;
  transform: scaleX(0);
  background-color: white;
  animation: loading 2s linear infinite;
}
.Loader span:nth-child(1) {
  transform: rotate(0);
}
.Loader span:nth-child(2) {
  transform: rotate(90deg);
}
.Loader span:nth-child(3) {
  transform: rotate(180deg);
}
.Loader span:nth-child(4) {
  transform: rotate(270deg);
}
.Loader span:nth-child(1):before {
  animation-delay: 0s;
}
.Loader span:nth-child(2):before {
  animation-delay: 0.5s;
}
.Loader span:nth-child(3):before {
  animation-delay: 1s;
}
.Loader span:nth-child(4):before {
  animation-delay: 1.5s;
}
@keyframes loading {
  0% {
    transform-origin: left;
    transform: scaleX(0);
  }

  25% {
    transform-origin: left;
    transform: scaleX(1);
  }

  25.00001% {
    transform-origin: right;
  }

  50%,
  100% {
    transform-origin: right;
    transform: scaleX(0);
  }
}
@media (min-width: 960px) {
  .inputsContainer {
    padding: 0;
  }
  .FormInputs {
    flex-direction: row;
    justify-content: space-between;
    width: 800px;
  }
  .FormButtons input {
    margin: auto;
    margin-left: 70px;
  }
  .ReCaptcha {
    margin: 0 70px 0 0;
  }
  .questionText {
    max-width: 60%;
  }
}
