.container {
  margin-top: 60px;
}
.presentacion {
  position: relative;
  width: 100%;
  height: 75vh;
  min-height: 500px;
}
.bgSlider {
  width: 100%;
  height: 100%;
  display: flex;
  filter: brightness(0.5);
  overflow-x: hidden;
}
@keyframes slide {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
.bgSlider img {
  animation: slide 75s linear infinite;
  transform: translateX(0);
}
.mainPresentacion img {
  display: none;
}
.mainPresentacion {
  width: 60%;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  color: white;
  line-height: 23px;
  letter-spacing: 0.5px;
  text-align: justify;
  font-weight: 200;
}
.bttnGeneric {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 45px;
  margin: 30px auto 0;
  color: white;
  font-weight: 500;
  border: 2px solid white;
  user-select: none;
  text-decoration: none;
}
.gallery {
  display: grid;
  gap: 5px;
  grid-template-columns: repeat(18, 1fr);
  padding: 5px 0;
  background-color: #0d0d0d;
}
.gallery img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.sliderGalleryContainer {
  position: relative;
  user-select: none;
  overflow: hidden;
}
.draggableSliderContainer {
  position: absolute;
  width: 50%;
  border-right: 2px solid white;
}
.draggableSliderBttn {
  position: absolute;
  top: 50%;
  right: -25px;
  cursor: pointer;
}
.renderq {
  bottom: 5px;
  position: fixed;
  right: 5px;
  z-index: 100;
  background-color: #fff;
  box-shadow: 0 0 9000px #fff;
  color: #000;
}
@media (min-width: 960px) {
  .container {
    margin-top: 0;
  }
  .mainPresentacion img {
    display: block;
    margin-bottom: 15px;
  }
  .mainPresentacion {
    width: 600px;
    left: 13.5%;
    transform: translatey(-50%);
  }
  .presentacion {
    height: 100vh;
    min-height: 500px;
  }
  .bttnGeneric {
    transition-property: color, background-color, box-shadow;
    transition-duration: 0.3s;
    transition-timing-function: ease;
    margin: 20px 0 0 0;
  }
  .bttnGeneric:hover {
    background-color: white;
    color: black;
    box-shadow: 0 0 9000px white;
  }
  .renderq {
    bottom: 150px;
    right: 50px;
  }
}
